.zindex-1 {
	z-index: 1;
	position: relative;
}

.background-deco {
	z-index: 0;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0.4;
}

.background-deco img {
	width: 100%;
	height: 100%;
}

.full-height {
	height: 100vh;
}

.mt--100 {
	margin-top: -100px;
}

.overflow-visible {
	overflow: hidden;
}
