.horizontal__project {
	overflow: visible;
	height: fit-content;
}
.horizontal__project div {
	overflow: visible;
}

.project__container__image {
	border-radius: 32px;
	background: linear-gradient(
		0deg,
		var(--nextui-colors-blue900),
		var(--nextui-colors-blue700) 50%
	);
	width: 200px;
	height: 200px;
	overflow: visible;
}
.project__container__image div {
	overflow: unset;
}

.container__image {
	position: absolute;
	left: 50%;
	top: 0%;
	transform: translateX(-50%);
	height: 120%;
	width: auto;
	max-width: unset;
	object-fit: cover;
}

.tech-circles-container:first-child {
	margin-left: 10px;
}

.tech-circle {
	border-radius: 50%;
	width: 44px;
	height: 44px;
	color: var(--nextui-colors-accent-2);
	background: var(--nextui-colors-gradient);
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-left: -16px;
}

.tech-circle svg {
	background-color: black;
	padding: 4px;
	border-radius: 50%;
	font-size: 40px;
}

.vertical-card-image-container {
	justify-content: flex-end;
}
.vertical-card-image {
	width: 70%;
	max-width: unset;
	object-fit: cover;
	vertical-align: end;
	margin-left: auto;
	margin-right: auto;
}

.tall {
	grid-row: span 2;
}

.wide {
	grid-column: span 2;
}
