.illustrations {
	width: 100%;
	height: 100%;
	position: relative;
}

.main__illustration {
	z-index: 1;
	position: relative;
	animation: main_illustration 10s ease-in-out infinite alternate-reverse;
}

.icon-3d {
	height: 80px;
	width: 80px;
	position: absolute;

	animation: animation 2s cubic-bezier(0.33333, 0.66667, 0.66667, 1) infinite
		alternate-reverse;
	z-index: 0;
}

@keyframes main_illustration {
	from {
		transform: rotate3d(0, 0, 1, -10deg);
	}
	to {
		transform: rotate3d(0, 0, 1, 10deg);
	}
}

@keyframes animation {
	from {
		transform: translateY(0);
	}
	to {
		transform: translateY(300px);
	}
}
